import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { NgDatepickerModule } from 'ng2-datepicker';


import { AppComponent } from './app.component';
import { SidebarComponent } from './core/sidebar/sidebar.component';
import { HeaderComponent } from './core/header/header.component';
import { AddSidebarComponent } from './core/add-sidebar/add-sidebar.component';
import { ProfileLoginComponent } from './core/sidebar/profile-login/profile-login.component';
import { MenuComponent } from './core/sidebar/menu/menu.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { PatientsComponent } from './patients/patients.component';
import { DoctorsComponent } from './doctors/doctors.component';
import { HospitalsComponent } from './hospitals/hospitals.component';
import { WardsComponent } from './wards/wards.component';
import { MedicaltechniciansComponent } from './medicaltechnicians/medicaltechnicians.component';
import { GroupsComponent } from './labs/groups/groups.component';
import { SpecimensComponent } from './labs/specimens/specimens.component';
import { LabsComponent } from './labs/labs/labs.component';
import { OrdersComponent } from './orders/orders.component';
import { ResultsComponent } from './results/results.component';
import { RulesComponent } from './securityusers/rules/rules.component';
import { UsersComponent } from './securityusers/users/users.component';
import { PatientAddComponent } from './patients/patient-add.component';
import { DoctorAddComponent } from './doctors/doctor-add.component';
import { HospitalAddComponent } from './hospitals/hospital-add.component';
import { MedicaltechnicianAddComponent } from './medicaltechnicians/medicaltechnician-add.component';
import { WardAddComponent } from './wards/ward-add.component';
import { GroupAddComponent } from './labs/groups/group-add.component';
import { SpecimenAddComponent } from './labs/specimens/specimen-add.component';
import { LabAddComponent } from './labs/labs/lab-add.component';
import { ResultDetailComponent } from './results/result-detail.component';
import { LoginComponent } from './login/login.component';
import { OrderDetailComponent } from './orders/order-detail.component';
import { PatientInfoComponent } from './patients/patient-info.component';
import { OrderPriceComponent } from './orders/order-price.component';


@NgModule({
  declarations: [
    AppComponent,
    SidebarComponent,
    HeaderComponent,
    AddSidebarComponent,
    ProfileLoginComponent,
    MenuComponent,
    DashboardComponent,
    PatientsComponent,
    DoctorsComponent,
    HospitalsComponent,
    WardsComponent,
    MedicaltechniciansComponent,
    GroupsComponent,
    SpecimensComponent,
    LabsComponent,
    OrdersComponent,
    ResultsComponent,
    RulesComponent,
    UsersComponent,
    PatientAddComponent,
    DoctorAddComponent,
    HospitalAddComponent,
    MedicaltechnicianAddComponent,
    WardAddComponent,
    GroupAddComponent,
    SpecimenAddComponent,
    LabAddComponent,
    ResultDetailComponent,
    LoginComponent,
    OrderDetailComponent,
    PatientInfoComponent,
    OrderPriceComponent
  ],
  imports: [
    BrowserModule,
    NgDatepickerModule,
    RouterModule.forRoot([
      { path: '', component: ResultsComponent },
      { path: 'dashboard', component: DashboardComponent },
      { path: 'orders', component: OrdersComponent },
      { path: 'orders/prices', component: OrderPriceComponent },
      { path: 'orders/details', component: OrderDetailComponent },
      { path: 'results', component: ResultsComponent },
      { path: 'results/details', component: ResultDetailComponent },
      { path: 'patients', component: PatientsComponent },
      { path: 'patients/add', component: PatientAddComponent },
      { path: 'patients/info', component: PatientInfoComponent },
      { path: 'doctors', component: DoctorsComponent },
      { path: 'doctors/add', component: DoctorAddComponent },
      { path: 'hospitals', component: HospitalsComponent },
      { path: 'hospitals/add', component: HospitalAddComponent },
      { path: 'wards', component: WardsComponent },
      { path: 'wards/add', component: WardAddComponent },
      { path: 'medicaltechnicians', component: MedicaltechniciansComponent },
      { path: 'medicaltechnicians/add', component: MedicaltechnicianAddComponent },
      { path: 'groups', component: GroupsComponent },
      { path: 'groups/add', component: GroupAddComponent },
      { path: 'specimens', component: SpecimensComponent },
      { path: 'specimens/add', component: SpecimenAddComponent },
      { path: 'labs', component: LabsComponent },
      { path: 'labs/add', component: LabAddComponent },
      { path: 'rules', component: RulesComponent },
      { path: 'users', component: UsersComponent },
      { path: 'login', component: LoginComponent }
    ], { useHash: true })
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



